    flex-direction: row;
    justify-content: space-evenly;  
    position: relative;
    background-color: rgb(82, 65, 82);
    background-image: url(/React-Weather-App/static/media/clouds.31ededdd.jpg);
    background-size: cover;
    margin: 0 auto;
    width: 100%;
  }

  .caption {
    font-size: 3em;
    color: #201b2b;
